* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html, body {
  font-family: sans-serif;
}

body {
  padding: 50px;
}

h1 {
  text-align: center;
  margin-bottom: 20px;
}

.wrapper {
  max-width: 400px;
  margin: 0 auto;
}

.header {
  background-color: #eee;
  border-radius: 15px;
  padding: 20px;
}

.add-row {
  display: flex;
}

.add-input, .add-button {
  height: 35px;
  color: #555;
  background-color: #fff;
  border: 2px solid #555;
  border-radius: 10px;
  font-size: 30px;
  line-height: 1;
}

.add-input {
  flex: auto;
  margin-right: 15px;
  padding: 5px;
  font-size: 20px;
}

.add-button {
  width: 35px;
  cursor: pointer;
  transition: background-color .4s;
}

.add-button:hover {
  background-color: #ccc;
}

.items {
  margin-top: 20px;
}

.item {
  background-color: #eee;
  border-radius: 10px;
  align-items: center;
  padding: 10px 20px;
  display: flex;
}

.item + .item {
  margin-top: 10px;
}

.item.item_done {
  color: #ccc;
}

.item-checkbox {
  flex: none;
  margin-right: 5px;
}

.item-content {
  max-width: 100%;
  white-space: nowrap;
  text-overflow: ellipsis;
  flex: auto;
  overflow: hidden;
}

.item-remove-button {
  width: 20px;
  height: 20px;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #555;
  border-radius: 5px;
  flex: none;
  transition: background-color .4s;
}

.item-remove-button:hover {
  background-color: #ccc;
}

