.wrapper {
  max-width: 400px;
  margin: 0 auto;
}
.header {
  background-color: #eee;
  padding: 20px;
  border-radius: 15px;
}
.add-row {
  display: flex;
}
.add-input,
.add-button {
  height: 35px;
  font-size: 30px;
  line-height: 1;
  border-radius: 10px;
  border: 2px solid #555;
  color: #555;
  background-color: #fff;
}
.add-input {
  flex: 1 1 auto;
  padding: 5px;
  font-size: 20px;
  margin-right: 15px;
}
.add-button {
  width: 35px;
  transition: background-color 0.4s;
  cursor: pointer;
  &:hover {
    background-color: #ccc;
  }
}
.items {
  margin-top: 20px;
}
.item {
  padding: 10px 20px;
  display: flex;
  align-items: center;
  background-color: #eee;
  border-radius: 10px;
  & + & {
    margin-top: 10px;
  }
  &.item_done {
    color: #ccc;
  }
}
.item-checkbox {
  flex: 0 0 auto;
  margin-right: 5px;
}
.item-content {
  flex: 1 1 auto;
  max-width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.item-remove-button {
  flex: 0 0 auto;
  width: 20px;
  height: 20px;
  border: 1px solid #555;
  border-radius: 5px;
  transition: background-color 0.4s;
  cursor: pointer;
  background-color: #fff;
  &:hover {
    background-color: #ccc;
  }
}
